<template>
  <ion-page>
    <div class="splash">
      <div class="container">
        <ion-spinner name="dots" class="loader" />
      </div>
    </div>
  </ion-page>
</template>

<script setup>
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { getFirestore, doc, getDoc } from "firebase/firestore";
import { onMounted } from "vue";
import { IonPage, IonSpinner } from "@ionic/vue";
import { useRouter } from "vue-router";

const router = useRouter();
const db = getFirestore();

const checkAuthState = () => {
  const auth = getAuth();
  onAuthStateChanged(auth, async (user) => {
    if (user) {
      const userDoc = await getDoc(doc(db, "users", user.uid));
      if (userDoc.exists()) {
        const userData = userDoc.data();
        const userRole = userData.role;
        const userClass = userData.kelas;

        if (userRole === "siswa" && !userClass) {
          router.push({
            name: "halamansettingUser",
            query: { message: "nokelas" },
          });
          return;
        }

        if (userRole === "admin") {
          router.push({ path: "/admin" });
        } else if (userRole === "orangtua") {
          router.push({ path: "/orangtua" });
        } else if (userRole === "guru") {
          router.push({ path: "/guru" });
        } else if (userRole === "petugas") {
          router.push({ path: "/petugas" });
        } else if (userRole === "siswa") {
          router.push({ path: "/siswa" });
        } else if (userRole === "umum") {
          router.push({ path: "/umum" });
        } else {
          router.push({ name: "login" });
        }
      } else {
        router.push({ name: "login" });
      }
    } else {
      router.push({ name: "login" });
    }
  });
};

onMounted(() => {
  checkAuthState();
});
</script>

